.trader-container {
    height: 100vh;
    width: auto;

    .trader-container-fluid {
        background-color: black;
        height: 100%;
        padding: 0px;
        .row {
            background-color: white;
            height: 100%;
            .container-left {
                background-color: red;
            }
            .container-right {
                background-color: blue;
            }
        }
    }
}
